import React from "react";
import Slider from "react-slick";

class ReactSlick extends React.Component {

    constructor(){
        super();
        this.state = {
          authors:[],
        };
    }

  render() {
    const settings = {
        dots: true,
        centerMode: true,
        variableWidth: true,
        slidesToShow: 4,
        autoplay: false,
        autoplaySpeed: 5000,
        afterChange: function(currentSlide) {
          //console.log("after change", currentSlide);
        },
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      };
   
    return (
        <Slider {...settings}>
            {this.state.authors}
        </Slider>
    )
      
  }

  componentDidMount(){

    fetch(`https://www.wattpad.com/api/v3/stories?ids=210110835,207917947,205675311`)
    .then(response => { return response.json()})
    .then(data=>{
      let authors = data.stories.map((author,i)=>{
          return(
            <div  key={i} className="cover-wrapper">
            <div className="cover-inner">
              <div className="cover-image-wrapper">
				                 <a href={author.url} target="_blank">
				           <img src={author.cover}  alt=" "/>
                </a>
              </div>

              <div className="cover-copy-wrapper">
                <p className="cover-title regular-txt black-txt text-right">{author.title}</p>
                <p className="cover-author light-txt black-txt text-right">{author.user.name}</p>
              </div>
            </div>
          </div>
          )
      })
      this.setState({authors:authors})
    })
    
  }




}

export default ReactSlick;